import { NextRequest } from 'next/server';
import { createLogger } from '@/lib/logger';
import { withCronJobTracking, type CronJobContext } from '@/lib/cron-job-wrapper';

/**
 * Scout Agent Cron Endpoint
 * 
 * Vercel Cron Job that discovers and tests under-used but promising routes:
 * - Checks facilitators with good external metrics but low internal usage
 * - Gently exercises these routes to generate path-level data
 * - Tags all traffic with agentType: "scout" for filtering
 * 
 * Schedule: Every 15 minutes (configured in vercel.json)
 * 
 * Environment Variables:
 *   - SCOUT_ENABLED: "true" to enable
 *   - SCOUT_NETWORK: Target network (default: eip155:8453)
 *   - SCOUT_MODE: "demo" or "live" (default: "demo")
 *   - SCOUT_MAX_TARGETS: Max targets per run (default: 2)
 *   - CRON_SECRET: Optional secret for authorization
 */

const logger = createLogger({ component: 'ScoutCron' });

// =============================================================================
// CONFIGURATION
// =============================================================================

const SCOUT_NETWORK = process.env.SCOUT_NETWORK ?? "eip155:8453";
const SCOUT_MODE = (process.env.SCOUT_MODE ?? "demo") as "demo" | "live";
const SCOUT_MAX_TARGETS = Number(process.env.SCOUT_MAX_TARGETS ?? 2);
const DISCOVERY_URL = process.env.SCOUT_DISCOVERY_URL ?? "https://discovery.nexflowapp.app/api/discovery/actions";
const ROUTER_URL = process.env.SCOUT_ROUTER_URL ?? "https://www.nexflowapp.app/api/x402/router/execute";

// Facilitators to scout (those with potential but less internal data)
const SCOUT_FACILITATORS = ["payai", "x402rs", "dexter"] as const;

// Network-specific facilitators
const NETWORK_FACILITATOR_MAP: Record<string, readonly string[]> = {
  "eip155:8453": ["payai"],              // Base mainnet - only PayAI supports this
  "eip155:84532": ["x402rs"],            // Base Sepolia - x402rs testnet
  "solana:mainnet": ["payai", "dexter"], // Solana mainnet
  "solana:devnet": ["dexter"],           // Solana devnet
};

// =============================================================================
// TYPES
// =============================================================================

interface ScoutLogEntry {
  timestamp: string;
  agentType: "scout";
  facilitatorId: string;
  actionId: string;
  actionUrl?: string;
  network: string;
  asset: string;
  status: "success" | "error" | "timeout" | "skipped";
  latencyMs: number;
  scoutReason: string;
  httpStatus?: number;
  errorCode?: string;
  errorMessage?: string;
  mode: "demo" | "live";
}

interface DiscoveryAction {
  id: string;
  name: string;
  domain: string;
  endpoint: string;
  methods: string[];
  pricing?: {
    amount: string;
    asset: string;
    network: string;
  };
}

interface RouterResponse {
  ok: boolean;
  mode: string;
  result?: {
    status: "success" | "error";
    facilitatorId?: string;
    errorCode?: string;
  };
  error?: string;
}

// =============================================================================
// UTILITIES
// =============================================================================

function pickFrom<T>(arr: readonly T[]): T {
  return arr[Math.floor(Math.random() * arr.length)];
}

function pickN<T>(arr: readonly T[], n: number): T[] {
  const shuffled = [...arr].sort(() => Math.random() - 0.5);
  return shuffled.slice(0, Math.min(n, arr.length));
}

// =============================================================================
// SCOUT LOGIC
// =============================================================================

async function fetchDiscoveryActions(): Promise<DiscoveryAction[]> {
  const url = new URL(DISCOVERY_URL);
  url.searchParams.set('network', SCOUT_NETWORK);

  const response = await fetch(url.toString(), {
    method: 'GET',
    headers: { 'Content-Type': 'application/json' },
    signal: AbortSignal.timeout(10000),
  });

  if (!response.ok) {
    throw new Error(`Discovery API error: ${response.status}`);
  }

  const data = await response.json();
  return data.actions ?? data ?? [];
}

async function executeScoutRoute(
  action: DiscoveryAction,
  facilitatorId: string
): Promise<ScoutLogEntry> {
  const startTime = Date.now();
  const timestamp = new Date().toISOString();

  try {
    const response = await fetch(ROUTER_URL, {
      method: 'POST',
      headers: { 'Content-Type': 'application/json' },
      body: JSON.stringify({
        resource: action.endpoint,
        method: action.methods?.[0] ?? 'POST',
        network: SCOUT_NETWORK,
        asset: action.pricing?.asset ?? 'USDC',
        maxSpend: action.pricing?.amount ?? '0.01',
        facilitatorStrategy: facilitatorId,
        mode: SCOUT_MODE,
        client: 'nexflow-scout',
        agentType: 'scout',
        useCase: 'cron-exploration',
      }),
      signal: AbortSignal.timeout(30000),
    });

    const latencyMs = Date.now() - startTime;
    const result: RouterResponse = await response.json();

    return {
      timestamp,
      agentType: 'scout',
      facilitatorId,
      actionId: action.id,
      actionUrl: action.endpoint,
      network: SCOUT_NETWORK,
      asset: action.pricing?.asset ?? 'USDC',
      status: result.ok && result.result?.status === 'success' ? 'success' : 'error',
      latencyMs,
      scoutReason: `Testing under-used facilitator: ${facilitatorId}`,
      httpStatus: response.status,
      errorCode: result.result?.errorCode,
      errorMessage: result.error,
      mode: SCOUT_MODE,
    };
  } catch (error) {
    const latencyMs = Date.now() - startTime;
    const isTimeout = error instanceof Error && error.name === 'TimeoutError';

    return {
      timestamp,
      agentType: 'scout',
      facilitatorId,
      actionId: action.id,
      actionUrl: action.endpoint,
      network: SCOUT_NETWORK,
      asset: action.pricing?.asset ?? 'USDC',
      status: isTimeout ? 'timeout' : 'error',
      latencyMs,
      scoutReason: `Testing under-used facilitator: ${facilitatorId}`,
      errorCode: isTimeout ? 'TIMEOUT' : 'NETWORK_ERROR',
      errorMessage: error instanceof Error ? error.message : 'Unknown error',
      mode: SCOUT_MODE,
    };
  }
}

// =============================================================================
// CRON HANDLER
// =============================================================================

async function scoutHandler(
  _request: NextRequest,
  context: CronJobContext
): Promise<{ ok: boolean; [key: string]: any }> {
  const { runId, requestId } = context;
  const results: ScoutLogEntry[] = [];

  logger.info({
    runId,
    requestId,
    network: SCOUT_NETWORK,
    mode: SCOUT_MODE,
    maxTargets: SCOUT_MAX_TARGETS,
    msg: 'Starting scout run',
  });

  // Fetch available actions
  const actions = await fetchDiscoveryActions();
  
  if (actions.length === 0) {
    logger.warn({ runId, msg: 'No discovery actions available for scouting' });
    return {
      ok: true,
      message: 'No actions available',
      results: [],
    };
  }

  // Get facilitators for this network
  const facilitators = NETWORK_FACILITATOR_MAP[SCOUT_NETWORK] ?? SCOUT_FACILITATORS;
  
  // Pick random targets
  const targetFacilitators = pickN(facilitators, SCOUT_MAX_TARGETS);
  const targetAction = pickFrom(actions);

  logger.info({
    runId,
    action: targetAction.name,
    facilitators: targetFacilitators,
    msg: 'Scout targets selected',
  });

  // Execute scout routes
  for (const facilitatorId of targetFacilitators) {
    const logEntry = await executeScoutRoute(targetAction, facilitatorId);
    results.push(logEntry);

    // Log each result
    logger.info({
      runId,
      ...logEntry,
      msg: `Scout route ${logEntry.status}`,
    });

    // Small delay between requests
    await new Promise(resolve => setTimeout(resolve, 1000));
  }

  // Summary
  const successCount = results.filter(r => r.status === 'success').length;
  const errorCount = results.filter(r => r.status === 'error' || r.status === 'timeout').length;

  logger.info({
    runId,
    total: results.length,
    success: successCount,
    errors: errorCount,
    msg: 'Scout run completed',
  });

  return {
    ok: true,
    network: SCOUT_NETWORK,
    mode: SCOUT_MODE,
    results,
    summary: {
      total: results.length,
      success: successCount,
      errors: errorCount,
    },
  };
}

// =============================================================================
// EXPORTS
// =============================================================================

export const dynamic = 'force-dynamic';
export const maxDuration = 60;

export const GET = withCronJobTracking(scoutHandler, {
  jobId: 'scout',
  enabledEnvVar: 'SCOUT_ENABLED',
  timeout: 55000, // 55s (leave buffer for wrapper overhead)
  minInterval: 14 * 60 * 1000, // 14 minutes (schedule is every 15 min)
});
